import {v1} from "uuid";
import {Dispatch} from "redux";
import {profileAPI} from "../api/api";

export type profilePagePropsType = {
    postData: postDataPropsType []
    profile: ProfileType
    status: string
}

export type ActionType =
    ReturnType<typeof addPostAC>
    | ReturnType<typeof setUserProfile>
    | ReturnType<typeof setStatusProfile>


export type postDataPropsType = {
    id: string
    message: string,
    likesCount: number
    avatar: string
}

export type ProfileType = {
    "aboutMe": string,
    "contacts": {
        "facebook": string,
        "website": string,
        "vk": string,
        "twitter": string,
        "instagram": string,
        "youtube": string ,
        "github": string,
        "mainLink": string
    },
    "lookingForAJob": boolean,
    "lookingForAJobDescription": string,
    "fullName": string,
    "userId": number,
    "photos": {
        "small": string,
        "large": string
    }
} | null


let initialState = {
    postData: [
        {
            id: v1(),
            message: "Hello",
            likesCount: 12,
            avatar: "data:image/jpeg;base64,"
        },
        {
            id: v1(),
            message: "Guys!",
            likesCount: 11,
            avatar: "data:image/jpeg;base64,"
        },
        {
            id: v1(),
            message: "This is my new post",
            likesCount: 3,
            avatar: "data:image/jpeg;base64,"
        },
        {
            id: v1(),
            message: "This is my new post Guys",
            likesCount: 44,
            avatar: "https://pbs.twimg.com/media/D9wcZfeX4AAUZi0.jpg"
        }
    ],
    profile: null,
    status: ""
}

export const profileReducer = (state: profilePagePropsType = initialState, action: ActionType): profilePagePropsType => {
    switch (action.type) {
        case "ADD-POST":
            let newPost: postDataPropsType = {
                id: v1(),
                message: action.newPostText,
                likesCount: 0,
                avatar: "https://sun9-55.userapi.com/impf/4OVa92OuK5A2PL1OkHkfDHRK41EaNgTpv860Tw/DVztYSAWFbA.jpg?size=512x512&quality=96&sign=2df645602452340721ae5fcaeffc49ae&type=album"
            }
            return {
                ...state,
                postData: [...state.postData, newPost]
            }
        case "SET-USER-PROFILE":
            return {...state, profile: action.profile}
        case "SET-STATUS":
            return {...state, status: action.status}
        default:
            return state;
    }

}

export const addPostAC = (newPostText: string) => ({type: "ADD-POST", newPostText} as const)
export const setUserProfile = (profile: ProfileType) => ({type: "SET-USER-PROFILE", profile} as const)
export const setStatusProfile = (status: string) => ({type: "SET-STATUS", status} as const)
export const setUserProfileThunk = (userId: string) => (dispatch: Dispatch) => {
    profileAPI.getProfile(userId)
        .then(data => {
            dispatch(setUserProfile(data))
        })
}

export const getStatus = (userId: string) => (dispatch: Dispatch) => {
    profileAPI.getStatus(userId)
        .then(response => {
            dispatch(setStatusProfile(response.data))
        })
}

export const updateStatus = (status: string) => (dispatch: Dispatch) => {
    profileAPI.updateStatus(status)
        .then(response => {
            if (response.data.resultCode === 0) {
                dispatch(setStatusProfile(status))
            }
        })
}